<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="hover" class="mt">
      <!--  ECharts  DOM -->
      <div id="main" style="width: 800px;height:400px;"></div>
    </el-card>
  </div>
</template>
<script>
  import echarts from "echarts";
  export default {
    async mounted() {
      const { data } = await this.$http.get("reports/type/1");
      console.log('data.data :>> ', data);
      if (data.meta.status !== 200) {
        return this.$notify({
          title: "",
          message: data.meta.msg,
          type: "error",
          duration: 1500,
        });
      }
      const target={};
      Object.assign(target, this.options,data.data);
      // console.log('target :>> ', JSON.stringify(target,null,4));
      // const result={...this.options,...data.data}
      // domecharts
      let myChart = echarts.init(document.getElementById("main"));
      myChart.setOption(target);
    },
    data() {
      return {
        options: {
          title: {
            text: ''
          },
          tooltip: {
            trigger: 'axis',
            axisPointer: {
              type: 'cross',
              label: {
                backgroundColor: '#E9EEF3'
              }
            }
          },
          grid: {
            left: '3%',
            right: '4%',
            bottom: '3%',
            containLabel: true
          },
          xAxis: [
            {
              boundaryGap: false
            }
          ],
          yAxis: [
            {
              type: 'value'
            }
          ]
        }

      };
    },
    methods: {},
  };
</script>
<style scoped>
  .mt {
    margin-top: 10px;
  }
</style>