<template>
  <div>
    <!--  -->
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card shadow="hover" >
      <!--  -->
      <el-row type="flex" justify="start" :gutter="20" class="mb">
        <el-col :span="7">
          <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getOrderList()">
            <el-button slot="append" icon="el-icon-search" @click="getUserList()"></el-button>
          </el-input>
        </el-col>
      </el-row>

      <el-table :data="orderList" border class="mb">
        <el-table-column type="index" label="#"></el-table-column>
        <el-table-column prop="order_number" label="" width="250px"></el-table-column>
        <el-table-column prop="order_price" label=""></el-table-column>
        <el-table-column prop="pay_status" label="">
          <template slot-scope="scope">
            <el-tag type="success" v-if="scope.row.pay_status==='1'"></el-tag>
            <el-tag type="danger" v-else></el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="is_send" label=""></el-table-column>
        <el-table-column prop="create_time" label="">
          <template slot-scope="scope">{{scope.row.create_time|dataFormat}}</template>
        </el-table-column>
        <el-table-column prop="role_name" label="">
          <template slot-scope="item">
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button
                @click="showEditDialog(item.row.id)"
                size="small"
                icon="el-icon-edit"
                type="primary"
                circle
              ></el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top" :enterable="false">
              <el-button
                size="small"
                type="warning"
                icon="el-icon-location"
                @click="showProgress"
                circle
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <!--  -->
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[3, 5, 8, 15]"
        :page-size="queryInfo.pagesize"
        layout="sizes, prev, pager, next, jumper"
        :total="total"
        background
      ></el-pagination>
    </el-card>
    <!--  -->

    <el-dialog
      @close="editDialogClear()"
      title=""
      :visible.sync="editDialogVisible"
      width="30%"
      center
    >
      <el-form :model="editForm" :rules="FormRules" ref="editFormRef" label-width="100px">
        <el-form-item label="/" prop="address1">
          <el-cascader
            v-model="editForm.address1"
            :options="cityData"
            :props="{ expandTrigger: 'hover' }"
            class="cascader"
          ></el-cascader>
        </el-form-item>
        <el-form-item label="" prop="address2">
          <el-input v-model="editForm.address2"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false"> </el-button>
        <el-button type="primary" @click="summitEdit()"> </el-button>
      </span>
    </el-dialog>

    <!--  -->
    <el-dialog title="" :visible.sync="progressVisible" width="50%" center>
      <el-timeline :reverse="reverse">
        <el-timeline-item
          v-for="(activity, index) in progressInfo"
          :key="index"
          :timestamp="activity.ftime"
        >{{activity.context}}</el-timeline-item>
      </el-timeline>
      <span slot="footer" class="dialog-footer">
        <el-button @click="progressVisible = false"> </el-button>
        <el-button type="primary" @click="progressVisible = false"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import cityData from "./citydata";
export default {
  data() {
    return {
      queryInfo: {
        query: "",
        pagenum: 1,
        pagesize: 8,
      },
      total: 0,
      editForm: {
        address1: [],
        address1: "",
      },
      orderList: [],
      FormRules: {
        address1: [{ required: true, message: "", trigger: "blur" }],
        address2: [
          { required: true, message: "", trigger: "blur" },
          {
            min: 1,
            max: 30,
            message: " 1  30 ",
            trigger: "blur",
          },
        ],
      },
      editDialogVisible: false,
      progressVisible: false,
      cityData: cityData,
      progressInfo: [],
       reverse: true,
    };
  },
  methods: {
    async getOrderList() {
      const { data } = await this.$http.get("orders", {
        params: this.queryInfo,
      });
      if (data.meta.status != 200) {
        return this.$message.error(data.meta.msg);
      }
      this.orderList = data.data.goods;
      this.total = data.data.total;
    },
    handleSizeChange(newsize) {
      this.queryInfo.pagesize = newsize;
      this.getOrderList();
    },
    handleCurrentChange(newpage) {
      this.queryInfo.pagenum = newpage;
      this.getOrderList();
    },
    showEditDialog(id) {
      console.log(id);
      this.editDialogVisible = true;
    },
    summitEdit() {
      this.$refs.editFormRef.validate(async (valid) => {
        if (!valid) {
          return;
        } else {
          this.editDialogVisible = false;
          this.$notify({
            title: "helloworld",
            message: "",
            type: "warning",
            duration: 1500,
          });
        }
      });
    },
    editDialogClear() {
      this.$refs.editFormRef.resetFields();
    },
    async showProgress() {
      const { data } = await this.$http.get("kuaidi/1106975712662");
      if (data.meta.status != 200) {
        return this.$message.error(data.meta.msg);
      }
      console.log("data.data :>> ", data.data);
      this.progressInfo = data.data;
      this.progressVisible = true;
    },
  },
  created() {
    this.getOrderList();
  },
};
</script>
<style scoped>
.el-breadcrumb {
  margin-bottom: 20px;
}
.mb {
  margin-bottom: 20px;
}
.cascader {
  width: 100%;
}
</style>>